import { styled } from "styled-components";
import { Button } from "../..";

const Mainwrapper = styled.div`
  display: flex;
  width: 30%;
  height: 250px;
  border-radius: 10px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  box-shadow: 5px 5px 5px 5px gray;
`

const WrapImgWrapper = styled.div`
    display: flex;
    justify-content: center;
    
`
const WrapTxtWrapper = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    
`

const ImgWrapper = styled.img`
    width: 45%;
    
`
const TextWrapper = styled.p<Textprops>`
    font-size: 20px;
    text-align: center;

`

const OpenChatCard = ( props: Textprops ) => {
    const {
        children, time, person, type = 'date', color = 'lightgrey', 
    } = props

    return(
        <Mainwrapper>
            <WrapImgWrapper>
                <ImgWrapper src='data:image/png;base64,' alt='openchat'/>
                <ImgWrapper src='data:image/png;base64,' alt='openchat'/>
            </WrapImgWrapper>
            <WrapTxtWrapper>
            <TextWrapper type={type} color={color}>
                두산 VS LG 06:00
            </TextWrapper>
            <TextWrapper type={type} color={color}>
                 204명
            </TextWrapper>
            <Button>참가하기</Button>
            </WrapTxtWrapper>
        </Mainwrapper>
    )
}

export default OpenChatCard

type Textprops = {
    children?: string,
    child?: string,
    time?: string,
    person?: string,

    color?: string,
    type?: string
}